import { Link } from "react-router-dom";

const Navbar = () => {
  return (
    <div>
      <nav className="navbar navbar-expand-lg bg-sd fixed-top">
        <div className="container">
          <Link className="navbar-brand text-white" to="/">CU Get Rekt</Link>
          <button className="navbar-toggler" type="button" data-bs-toggle="collapse" data-bs-target="#navbarSupportedContent"
            aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
            <span className="navbar-togglear-icon"></span>
          </button>
          <div className="collapse navbar-collapse" id="navbarSupportedContent">
            <ul className="navbar-nav me-auto mb-2 mb-lg-0">
              <li className="nav-item">
                <Link className="nav-link text-white" aria-current="page" to="#"></Link>
              </li>
              <li className="nav-item">
                <Link className="nav-link text-white" to="#"></Link>
              </li>
              <li className="nav-item text-white dropdown">
                <Link className="nav-link dropdown-toggle text-white" to="#" role="button" data-bs-toggle="dropdown"
                  aria-expanded="false">
                  
                </Link>
                <ul className="dropdown-menu bg-sd">
                  <li className="py-1"><Link className="bg-dark-on-hover dropdown-item text-white" to="#">Blog</Link></li>
                  <li className="py-1"><Link className="bg-dark-on-hover dropdown-item text-white" to="#">Review</Link>
                  </li>
                  <li>
                    <hr className="dropdown-divider" />
                  </li>
                  <li><Link className="dropdown-item text-white" to="/"></Link></li>
                </ul>
              </li>
            </ul>
            <div className="d-none d-lg-block">
              <ul className="navbar-nav mr-auto mb-2 mb-lg-0">
                <li className="nav-item me-auto">
                  <Link className="nav-link text-white" to="/Login"> <i className="fa-solid fa-user"></i>&nbsp; </Link>
                </li>
              </ul>
            </div>
            <div className="d-grid gap-1 mb-auto d-block d-lg-none">
              <Link className="btn btn-danger bg-pm nav-link text-white" to="/Login">
                 </Link>
            </div>
          </div>
        </div>
      </nav>
    </div>
  );
}

export default Navbar;
